






















d3 = require 'd3'
_  = require 'lodash'

Utils = require '../utils/Utils'

module.exports =
    name: 'slider-base'
    props: ['label', 'value', 'max', 'min', 'step', 'bounded']
    mixins: ['Utils']
    methods:
        round: (val) -> Math.round(100 * val) / 100
        update: (value) ->

            _number = Number(value)

            if _.isNaN(_number)
                number = 0
            else
                number = _number

            if @bounded
                if number > @max
                    number = _.min([@max, number])
                else if number < @min
                    number = _.max([@min, number])

            @$emit('input', number)

        increment: () ->
            newValue = @round(Number(@value) + Number(@step))

            @update(newValue)

        decrement: () ->
            newValue = @round(Number(@value) - Number(@step))

            @update(newValue)

        wheel: (direction) ->

            if direction > 0
                @increment()
            if direction < 0
                @decrement()




