






































Graph = require './components/Graph'
Utils = require './utils/Utils'

randomcolor = require 'randomcolor'

jstat = require 'jstat'

module.exports =
  name: 'app',
  components: {Graph}
  mixins: [Utils]
  methods:
      getColor: () -> randomcolor('luminosity': 'dark')
  data: () ->
    jstat: jstat
    themeColor: {backgroundColor: @getColor()}
    beta: [
        mode: 'pdf'
        area: false
        dots: false
        p: [
            @createParam('alpha', 1.9, 0, 20, 0.01)
            @createParam('beta', .73, 0, 10, 0.01)]
    ]
    gamma: [
        mode: 'pdf'
        area: false
        dots: false
        p: [
            @createParam('shape', .5, 0, 1, 0.01)
            @createParam('scale', 1, 0, 20, 0.01)]
    ]
    normal: [
        mode: 'pdf'
        area: false
        dots: false
        p: [
            @createParam('mean', .5, 0, 1, 0.01)
            @createParam('std', .1, 0, 1, 0.01)]
    ]
    poisson: [
        mode: 'pdf'
        area: false
        dots: false
        discrete: true
        connect: false
        p: [
            @createParam('lambda', 5.5, 0, 20, 1)]
    ]
    negbin: [
        mode: 'pdf'
        area: false
        dots: false
        discrete: true
        connect: false
        p: [
            @createParam('n', 4, 0, 10, 1)
            @createParam('p', .3, 0, 1, 0.01, true)]
    ]
    bin: [
        mode: 'pdf'
        area: false
        dots: false
        discrete: true
        connect: false
        p: [
            @createParam('n', 10, 0, 100, 1)
            @createParam('p', .8, 0, 1, 0.01, true)]
    ]
    chisq: [
        mode: 'pdf'
        area: false
        dots: false
        p: [
            @createParam('df', 3, 0, 40, 1)]
    ]
    exp: [
        mode: 'pdf'
        area: false
        dots: false
        p: [
            @createParam('rate', .4, 0, 1, .01)]
    ]



